.card {
    width: 300px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.card-img {
    width: 100%;
    height: 150px;
    object-fit: cover;
}

.card-info {
    padding: 15px;
}

.card-name {
    margin: 10px 0 5px;
    font-size: 1.5em;
}

.card-role {
    color: #606060;
    font-size: 0.95em;
}

.social-icons {
    display: flex;
    justify-content: center;
    padding-top: 10px;
}

.social-link img {
    width: 30px;
    height: 30px;
    margin: 0 5px;
}

.social-link {
    display: inline-block;
}
